import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { Vendor } from '../vendor/vendor';
import { SaveVendorService } from './save-vendor.service';


@Component({
  selector: 'app-save-vendor',
  templateUrl: './save-vendor.component.html',
  styleUrls: ['./save-vendor.component.css']
})
export class SaveVendorComponent implements OnInit {

  constructor(private service : SaveVendorService,private router:Router,private route: ActivatedRoute) { }
  vendor= new Vendor();
  vendors:Vendor[];
  vendorId: any;

  ngOnInit() {
    this.route.params.subscribe(params => this.getVendorbyId(params['id']));
    this.resetForm();
  }

  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={ 
    vendorId:null,
    vendorName:'',
    address:'',
    location:null,
    service:'',
    pincode:'',
    isActive:'',
    contactId:null,
    contactName:'',
    department:'',
    email:'',
    phone:''                      
    }                                  
  }

 // insert new vendor

  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }

  // to get all vendors

  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  // navigate to vendor page

  viewVendor(){
    this.router.navigate(['vendor'])
  }
  getVendorbyId(vendorId: string) {
    this.vendorId = vendorId;
    console.log("Vendor Id " + vendorId);
    this.service.getVendorbyId(vendorId)
      .subscribe((searchData) => {
        this.vendor = searchData;
        console.log(searchData);
      }, (error) => {
        console.log(error);
      });
  }

  // navigate to vendor page

  back(){
    this.router.navigate(['vendor'])
  }
  
  // for logging out

  logout(){
    this.router.navigate([''])
  }

}
