import { Component, OnInit } from '@angular/core';
import { LoginService } from './login.service';
import { Router } from '@angular/router';
import { AppComponent } from '../app.component';
import { Login } from './login';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  login = new Login;
  userId: number;
  username: string;
  password: string;

  constructor(private loginService:LoginService,private router:Router,private app:AppComponent) { }

  ngOnInit() {
    this.resetForm()
  }
   //to reset the form
   resetForm(form?:NgForm) {
    if(form!=null)
    form.resetForm();
    this.loginService.formData={
     userId:null,
      username:'',
      password:'',
    }
  }
  // to call login validation on form submission

  OnSubmit(form:NgForm){
    this.validLogin(form);

  }

  // for login validation

    validLogin(form:NgForm){
      this.loginService.validate(form.value).subscribe(data=>{
      this.login=data; 
      this.router.navigate(['vendor']);
      },(error)=>{
      console.log("error");
      window.alert("Wrong username or password");
      });
      
        

}
  

}
