import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { Vendor } from '../vendor/vendor';

@Injectable({
  providedIn: 'root'
})
export class SaveVendorService {
  formData:Vendor;
  vendors: Vendor[];
  vendor=new Vendor();
  constructor(private _httpService : HttpClient) { }

  // to insert a vendor

  insertVendor(vendor:Vendor){
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    console.log('before save');
 
    
    
    if(vendor.vendorId)
    {
      return this._httpService.put(environment.APIUrl +'/updatevendor/', body, options);
    }else{
      return this._httpService.post(environment.APIUrl+'/insertvendor',body, options);
    }

  }
    // get all vendors
    
    getAllVendors():  Observable<Vendor[]>{
      return this._httpService.get<Vendor[]>(environment.APIUrl +'/vendor/null')
    }

    // to get vendor by id
    
    getVendorbyId(vendorId: any): Observable<Vendor> {
      return this._httpService.get<Vendor>(environment.APIUrl + '/vendors/' + vendorId)
    }

    }
    
    
  



