import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { Vendor } from './vendor';
import { VendorService } from './vendor.service';


@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.css']
})
export class VendorComponent implements OnInit {
  vendors: Vendor[];
  vendor=new Vendor();
  searchid: any;
  view:any;
  constructor(private _vendorService:VendorService, private router :Router,private route:ActivatedRoute) { }

  ngOnInit() {
    this.getAllVendors();
  }

  //to get all vendors

  getAllVendors(): void{
    this._vendorService.getVendors()
    .subscribe((vendorData)=>{
      this.vendors=vendorData
    }, (error)=>{
      console.log(error);
    });
  }

  // to navigate to insertion form

  addVendor(){
    this.router.navigate(['save-vendor'])
  }

  // navigate to login page

  login(){
    this.router.navigate([''])
  }

  // for logging out

  logout(){
    this.router.navigate(['login'])
  }

  // to get contact details

  getContactDetails(data:any){
    console.log(data);
    this.view=data.vendorId;
    this.viewContact;
    this.viewContact(this.view);
  //  this._vendorService.getContact(data.vendor_id)
  //  .subscribe((data)=>{
  //   this.vendors=data
  //   console.log(this.vendors);
    // this.router.navigate(['view-contact',id])
    // this.viewContact()
  // }, (error)=>{
  //   console.log(error);
  // });

}
viewContact(data){
  this.router.navigate(['view-contact',this.view])
}

// navigate to editing page

editVendor(id:any){
  this.router.navigate(['save-vendor',id])
}

//to get all vendors
getVendors() {
  this._vendorService.getVendors()
    .subscribe((data) => {
      this.vendors = data;
      console.log(data);
    }, (error) => {
      console.log(error);
    });
}

// for disabling 

disable()
{
  console.log("inside disable" +this.searchid);
  this._vendorService.disableVendor(this.searchid)
  .subscribe((data)=>{
    console.log(data);
    this.getVendors();
  },(error)=>{
    console.log(error);
  });  
}

//to get searchid

onSelect(selectedVendor: any) {
  console.log("Selected item Id:", selectedVendor.vendorId);
  this.searchid = selectedVendor.vendorId;
}

}
